.sponsorsHeader, .body, .sponsors {
	align-items: center; 
	display: flex; 
	flex-direction: column;
}
.divider-parent, .sponsor1, .sponsor2, .sponsorButtons {
	align-items: center; 
	display: flex; 
	flex-direction: row; 
	justify-content: center;
}

.dividerParent img {
	width: 30vh;
	aspect-ratio: 3 / 2;
}

.photos {
    z-index: 1;
    width: 40vw;
    padding: 5vw;
    position: relative;
}

.bodyStrap {
    background-color: #707070;
    border-top: #ff0000 solid 1px;
    border-bottom: #4df4e4 solid 1px;
    font-size: 1.5rem;
    margin: 0;
    text-align: center;
    width: 100vw;
}

.sponsor1, .sponsor2 {
    color: white; 
    font-family: 'cr-book';
    font-size: 1.5rem;
    height: 350px;
    margin: 5rem;
    width: 80vw;
}
.sponsor1 > *, .sponsor2  > * {margin: 2rem;}
.sponsor1 img {
	aspect-ratio: 1 / 1; 
	position: relative; 
	width: 20rem;
}
.sponsor2 img {
	aspect-ratio: 4.23 / 1; 
	position: relative; 
	width: 20rem;
}

.sponsor1 p, .sponsor2 p {
    text-align: left;
    width: 50rem;
}
.sponsor1 span, .sponsor2 span {
    height: 150px;
    border-left: white solid 1px;
}

.sponsorButtons {margin: 3rem;}
.sponsorButtons button {
    background: transparent;
    border-style: 2px solid;
    border-image: linear-gradient(to right, #ff0000 50%, #4df4e4 50%);
    border-image-slice: 1;
    color: white; 
    font-family: 'cr-book';
    font-size: 1.12vw;
    height: 3.4rem;
    margin: 2rem;
    min-width: 12vw;
}
.sponsorButtons button span {
    align-items: center;
    border: 2px white solid;
    display: flex;
    height: 105%;
    justify-content: center;
    margin: auto;
    margin-top: -2%;
    overflow: hidden;
    width: 100%;
	margin-bottom: 5vh;
}

@media (max-width: 70em) {
    .sponsorDivider {
        margin: 4rem; 
        width: 10rem;
    }

    .sponsor1, .sponsor2 {
        flex-direction: column;
        height: 500px;
        margin: 5rem;
    }
    .sponsor1 > *, .sponsor2  > * {margin: 0.5rem;}
    .sponsor1 img {
        width: 50vw;
        max-width: 15rem;
    }
    .sponsor2 img {
        width: 70vw;
        max-width: 30rem;
    }
    .sponsor1 p, .sponsor2 p {
        text-align: center;
        width: 100vw;
        max-width: 25rem;
    }
    .sponsor1 span, .sponsor2 span {
        height: 0;
        max-width: 150px;
        border-bottom: white solid 1px;
    }

}
